.block-cards {
	padding: 10px;
	border-radius: 8px;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.05);
	background-color: var(--white);
	border: solid 1.5px #292A36;
	margin: 10px;
	width: 355px;
	max-width: 355px;
	min-width: 355px;
	overflow-x: hidden;
}

button {
	width: 250px;
	min-width: 250px;
	max-width: 250px;
	overflow-x: hidden;
	font-size: 16px;
	padding: 9px;
	border-radius: 8px;
	display: flex;
	border: none;
	cursor: pointer;
	background-color: var(--blue);
	color: var(--white);
	transition: all .3s;
	z-index: 1;
	overflow: hidden;
	// display: inline-block;
	position: relative;
	margin-top: auto;
	&after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--blue);
		border-radius: 8px;
		z-index: -2;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 0%;
		height: 100%;
		background-color: var(--lightblue, 15%);
		transition: all .3s;
		border-radius: 8px;
		z-index: -1;
	}

	&:hover {
		color: #fff;
		&:before {
			width: 100%;
		}
	}
}

